@charset "utf-8";

.form{
  display: flex;
}
.form input{
  font-size: 1.25rem;
  border:none;
  outline: none;
  border-bottom: 1px solid #ff9900;
  /* 버튼의 가로너비를 제외한 나머지 영역을 차지한다. */
  /* flex:content; */
  flex:1;
}
.create_btn{
  padding: 1rem 2rem;
  margin-left: 1rem;
  border-radius: 3px;
  color:#fff;
  font-weight: bold;
  background: #d86a02;
  border:none;
  font-size: 1.1rem;
}
.create_btn:hover{
  background: #f77d0c;
}