@charset "utf-8";

.todo_item{
  display: flex;
  align-items: center;
  cursor: pointer;
}
.todo_item:hover{
  background:#ccc;
}
.todo_item:hover .remove{
  opacity: 1;
}
.remove{
  opacity: 0;
  font-weight: bold;
  color:#f00;
  margin-right: 0.5rem;
}
.todo_text{
  flex:1;
  line-height: 40px;
  border-bottom: 1px solid #ccc;
}
.checked{
  text-decoration: line-through;
  color:#00f;
}
.check_mark{
  font-size: 1.5rem;
  line-height: 1rem;
  color:#f00;
  font-weight: bold;
}