@charset "utf-8";
body{
  margin: 0; padding: 0;
  background: #ebebeb;
  font-family: "맑은 고딕";
}

.todo_list_template{
  width: 90%;
  background: #fff;
  margin: 4rem auto 0 auto;
  box-shadow: 0px 3px 6px #ccc;
}
.title{
  padding: 2rem;
  font-size:2.5rem;
  text-align: center;
  font-weight: bold;
  background: #ff9900;
  color:#fff;
}
.form_wrapper{
  padding: 1rem;
  border-bottom: 1px solid #ff9900;

}
.todos_wrapper{
  min-height: 5rem;
}